@import url("https://fonts.googleapis.com/css?family=Open+Sans:700|Raleway:400,500|Roboto:700,900&display=swap");

$brand-color: #ffcc00;
$text-color: #333;
$title-color: #666;

$b-size: 1px;

$text-ff: "Raleway", sans-serif;
$title-ff: "Roboto", sans-serif;
$btn-ff: "Open Sans", sans-serif;

$height-icon: 15px;
$width-line: 35px;
$height-line: 4px;

$transition-time: 0.4s;
$rotation: 45deg;
$translateY: ($height-icon / 2);
$translateX: 0;
