@import url("https://fonts.googleapis.com/css?family=Oswald:300,400&display=swap");

@import "bootstrap/bootstrap";
@import "variables";
@import "mixins";

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a {
  transition: all 0.25s ease-in;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  background-color: #f7f8f3;
}

p {
  line-height: 2rem;
}

.container {
  max-width: 1180px;
  width: 98%;
  margin: 0 auto;
  &--accordion {
    max-width: 580px;
  }
}

section {
  padding: 100px 0;
}

.section-sm {
  padding: 50px 0;
}

.header-bg {
  height: 100vh;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.2), transparent),
    url(../img/bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding: 0;
  overflow: hidden;

  &__text {
    max-width: 650px;
    margin-left: auto;
    color: $white;
    padding: 250px 0 0 0;
  }
  &__chevrov {
    color: $white;
    font-size: 2.5rem;
    position: absolute;
    bottom: 2%;
    left: 50%;
    transform: translate(-50%, 0%);
  }
  &__aside {
    display: inline-block;
    color: $white;
    font-size: 1.625rem;
    text-transform: uppercase;
    transform: rotate(270deg);
    position: absolute;
    bottom: 20%;
    left: -5%;
    &::before {
      content: "";
      display: inline-block;
      vertical-align: super;
      width: 100px;
      height: 2px;
      background-color: #fff;
      margin-right: 30px;
    }
  }
}

#search {
  cursor: pointer;
  display: flex;
  align-items: center;
  ion-icon {
    transition: all 0.25s ease-in-out;
  }
  @include hover {
    ion-icon {
      color: $primary;
    }
  }
}

.login {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;

  &__btn {
    list-style-type: none;
    margin: 0;
    color: white;
    padding: 25px 32px;
    border-left: 1px solid rgba($white, 0.4);

    &--search {
      background-color: transparent;
      border: none;
      text-transform: uppercase;
      padding: 0;
      @include hover {
        color: $primary;
        outline: none;
        border: none;
      }
    }
    & a {
      display: flex;
      align-items: center;
    }
    & ion-icon {
      font-size: 1.5rem;
      margin-right: 7px;
    }
    & span {
      vertical-align: middle;
      line-height: 25px;
    }
  }
  &__input {
    width: 0;
    opacity: 0;
    transition: all 1s ease-in;
    padding: 0;
    background-color: transparent;
    border: none;
    outline: none;
    border-bottom: 1px solid rgba($white, 0.4);
    @include hover {
      border-color: $primary;
    }
  }
  &__svg {
    display: none;
  }
}

.btn-after {
  line-height: 20px;

  & span {
    &:after {
      content: "";
      display: inline-block;
      width: 45px;
      height: 2px;
      background-color: #fff;
      vertical-align: super;
      margin-left: 15px;
      transition: all 0.25s ease-in-out;
    }
  }
  @include hover {
    & span:after {
      content: "";
      width: 0;
      background-color: $primary;
    }
  }
}

.link {
  display: block;
  letter-spacing: 5px;
  & span {
    vertical-align: middle;
  }
  & ion-icon {
    vertical-align: middle;
  }
}

#searchBlock {
  width: 143px;
  transition: all 0.7s ease-in-out;
}

#navBar {
  transition: all 0.7s ease-in;
}

.what {
  max-width: 700px;
  margin: 0 auto;
  border: 12px solid $secondary;
  padding: 40px;
  &__img {
    display: block;
    margin: 0 auto;
  }
  &__logos {
    display: flex;
    justify-content: space-around;
  }
}

.slider {
  position: relative;
  &__slide {
    background-color: #fff;
    & ion-icon {
      color: $primary;
      fill: $primary;
    }
  }
  &__slide-product {
    border: 1px solid $secondary;
    height: 470px;
  }
  &__slide-price {
    border: 1px solid #e1e1e1;
    border-top: none;
    background-color: #f1f1f1;
    display: flex;
    align-items: center;

    & span {
      display: inline-block;
      flex-basis: 65%;
      letter-spacing: 3px;
    }
    & .btn-after {
      background-color: $primary;
      border: 1px solid $primary;
      width: 190px;
      & span {
        letter-spacing: normal;
      }
      @include hover {
        color: #fff;
      }
    }
  }
  &__arrow {
    fill: $primary;
    position: absolute;
    bottom: -15%;
    cursor: pointer;
  }
  &__arrow-prev {
    left: 45%;
    transform: rotate(180deg);
    @include hover {
      transform: rotate(180deg) scale(1.3);
    }
  }
  &__arrow-next {
    right: 45%;
    @include hover {
      transform: scale(1.3);
    }
  }
}

.faq {
  & h2 {
    font-size: 0;
  }
  &__btn {
    font-size: 1.125rem;
    width: 100%;
    height: 100%;
    text-align: left;
    padding: 0;
    line-height: 1;
    border: none;
    padding: 30px 0;
    font-weight: 100;
    letter-spacing: 2px;
    @include hover {
      background-color: transparent;
    }
    &:before {
      content: url(../img/plus.svg);
      margin-right: 20px;
    }
  }
}

.big-slider {
  overflow: hidden;
  position: relative;
  &__slider-content {
    text-align: center;
    max-width: 800px;
    max-height: 300px;
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $white;
    font-weight: 100;
    letter-spacing: 4px;

    & p {
      font-size: 1.375rem;
      line-height: 1.8;
    }
  }

  &__author {
    vertical-align: middle;
    line-height: 1;
    padding: 0;
    &::before {
      content: "";
      display: inline-block;
      vertical-align: super;
      line-height: 1;
      margin-right: 20px;
      width: 45px;
      height: 2px;
      background-color: $white;
    }
  }

  & ion-icon {
    color: $secondary;
  }

  & img {
    width: 100%;
  }

  &__aside {
    color: $white;
    font-weight: 100;
    letter-spacing: 4px;
    position: absolute;
    left: 0%;
    bottom: 20%;
    transform: rotate(90deg);
  }
  &__arrow {
    cursor: pointer;
    fill: $white;
    z-index: 5;
    padding: 35px 40px;
    position: absolute;
    bottom: 0;
    border: 1px solid $white;
    @include hover {
      background-color: #fff;
      fill: $primary;
    }
    &-next {
      left: 50%;
    }
    &-prev {
      right: 50%;
      transform: rotate(180deg);
    }
  }
}

.footer {
  &__logo {
    font-size: 3.75rem;
    color: $primary;
    display: block;
    text-align: center;
    position: relative;
    &::before,
    &::after {
      content: "";
      width: 370px;
      height: 2px;
      background-color: $primary;
      position: absolute;
      top: 50%;
    }
    &::before {
      left: 0;
    }
    &::after {
      right: 0;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
  }
  &__menu {
    padding: 0;
    margin: 0;
    list-style-type: none;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 100;
    text-align: center;
    font-size: 0.875rem;
    flex-basis: 25%;
  }
  &__menu-item {
    margin-top: 15px;
    & a {
      color: $primary;
      @include hover {
        text-shadow: 0px 5px 10px $primary;
      }
    }
  }
  &__social-block {
    flex-basis: 30%;
  }
  &__socials {
    padding: 0;
    margin: 0 auto;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    width: 50%;
    height: 45px;
    position: relative;
    &:before {
      content: "";
      width: 2px;
      height: 55px;
      background-color: $primary;
      position: absolute;
      bottom: -190%;
      left: 50%;
    }
  }
  &__social {
    font-size: 1.5rem;
    & a {
      color: $primary;

      @include hover {
        font-size: 1.7rem;
      }
    }
  }
}

.subscribe {
  &__title {
    margin-top: 120px;
    margin-bottom: 20px;
    text-transform: uppercase;
    text-align: center;
  }
  & input {
    border: none;
  }
  & button {
    border: none;
    position: relative;
    &:before {
      content: "";
      height: 85%;
      border-left: 2px solid $secondary;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
    }
  }
  &__copyright {
    margin-top: 50px;
    display: block;
    font-size: 0.7rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    color: $primary;
  }
}

.tab {
  &__text-block {
    max-height: 350px;
    overflow: hidden;
  }
  &__img {
    & img {
      margin-top: 30px;
      max-width: 100%;
      height: auto;
    }
  }
}
@media screen and (max-width: 1150px) {
  .big-slider {
    &__arrow {
      display: none;
    }
  }
}

@media screen and (max-width: 992px) {
  html {
    font-size: 14px;
  }
  .header-bg {
    &__aside {
      left: -12%;
      bottom: 15%;
    }
  }

  .nav-tabs {
    & .nav {
      flex-wrap: nowrap;
      &-link {
        font-size: 1.5rem;
        letter-spacing: 5px !important;
      }
    }
  }
  .slider {
    &__arrow {
      &-prev {
        left: 40%;
      }
      &-next {
        right: 40%;
      }
    }
  }

  .big-slider {
    &__aside {
      font-size: 1.2rem;
      left: -2%;
      bottom: 30%;
    }
    &__slider-content {
      max-height: 210px;
      // overflow: hidden;
    }
  }
  .swiper-slide {
    & p {
      font-size: 1rem;
    }
  }
  .footer {
    &__logo {
      &:before {
        width: 220px;
      }
      &:after {
        width: 220px;
      }
    }
  }
  .input-group {
    width: 50%;
  }
  #faqs {
    padding: 60px 0;
  }
  .login {
    &__btn {
      padding: 18px 25px;
      width: 80px;
      &--search {
        padding: 0 10px;
      }
    }
    &__svg {
      display: block;
      fill: $white;
      @include hover {
        fill: $primary;
      }
    }

    &__name {
      display: none;
    }
  }
  #searchBlock {
    width: 80px;
    & ion-icon {
      font-size: 1.8rem;
      margin: 0 auto;
    }
  }
  .tab {
    &__text-block {
      max-height: 300px;
      overflow: hidden;
    }
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 12px;
  }

  section {
    padding: 60px 0;
  }
  .collapse {
    display: block;
  }
  .navbar {
    padding: 20px 0 0 0;
    background-color: rgba(110, 119, 74, 0.9);
    position: absolute;
    top: -50%;
    position: fixed;
  }
  .login {
    max-width: 400px;
    margin: 0 auto;
    padding: 0;

    &__logo {
      display: none;
    }
    &__btn {
      border: none;
      margin: 0 auto;
    }
  }
  #searchBlock {
    & ion-icon {
      font-size: 2.5rem;
    }
  }

  header {
    .navbar-collapse {
      text-align: center;
      font-size: 2.2rem;
    }

    .nav-link {
      width: 25%;
      margin: 0 auto;
    }
  }
  h1 {
    letter-spacing: 6px;
  }
  h3 {
    letter-spacing: 4px;
  }
  .header-bg {
    &__text {
      padding-top: 200px;
      span {
        letter-spacing: 3px;
      }
    }
  }

  .what {
    letter-spacing: 2px;
    &__logos {
      flex-wrap: wrap;
    }
    &__img {
      margin-top: 20px;
    }
  }

  .hamburger {
    position: absolute;
    top: 0;
    right: 7%;
    z-index: 1100;
    position: fixed;
    cursor: pointer;
  }

  .nav-tabs {
    .nav-link {
      padding: 5px 0;
      letter-spacing: 4px !important;
    }
  }
  .tab-content {
    & h3 {
      text-align: center;
    }

    & p {
      letter-spacing: 0.8px;
      font-size: 1.4rem;
    }
    & img {
      display: block;
      margin: 30px auto;
      width: 95%;
    }
  }

  .header-bg__chevrov {
    font-size: 5rem;
  }

  .tab {
    &__text-block {
      max-height: 200px;
      overflow: hidden;
    }

    &__img-wrap {
      display: flex;
    }
  }
  #hamburger-icon {
    width: $width-line;
    height: $height-icon;
    position: relative;
    display: block;
    margin: ($height-icon * 2) auto $height-icon auto;

    .line {
      display: block;
      background: $white;
      width: $width-line;
      height: $height-line;
      position: absolute;
      left: 0;
      border-radius: ($height-line / 2);
      transition: all $transition-time;
      -webkit-transition: all $transition-time;
      -moz-transition: all $transition-time;
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);

      &.line-1 {
        top: 0;
      }
      &.line-2 {
        top: 50%;
      }
      &.line-3 {
        top: 100%;
      }
    }
    &:hover,
    &:focus {
      .line-1 {
        transform: translateY($height-line / 2 * -1);
        -webkit-transform: translateY($height-line / 2 * -1);
        -moz-transform: translateY($height-line / 2 * -1);
      }
      .line-3 {
        transform: translateY($height-line / 2);
        -webkit-transform: translateY($height-line / 2);
        -moz-transform: translateY($height-line / 2);
      }
    }
    &.active {
      .line-1 {
        transform: translateY($translateY) translateX($translateX)
          rotate($rotation);
        -webkit-transform: translateY($translateY) translateX($translateX)
          rotate($rotation);
        -moz-transform: translateY($translateY) translateX($translateX)
          rotate($rotation);
      }
      .line-2 {
        opacity: 0;
      }
      .line-3 {
        transform: translateY($translateY * -1) translateX($translateX)
          rotate($rotation * -1);
        -webkit-transform: translateY($translateY * -1) translateX($translateX)
          rotate($rotation * -1);
        -moz-transform: translateY($translateY * -1) translateX($translateX)
          rotate($rotation * -1);
      }
    }
  }
}

@media screen and (max-width: 576px) {
  html {
    font-size: 10px;
  }

  h2 {
    letter-spacing: 2px;
  }

  h3 {
    letter-spacing: 1px;
    font-size: 1.7rem;
  }

  .header-bg {
    &__aside {
      left: -18%;
      bottom: 12%;
    }

    &__text {
      span {
        font-size: 1.2rem;
      }
    }
  }
  header {
    .nav-link {
      width: 50% !important;
    }
  }
  .nav-tabs {
    flex-direction: column;
    text-align: center;
    .nav-link {
      margin: 5px auto 0 !important;
    }
  }
  .tab-content {
    img {
      display: none;
    }
  }
  .slider {
    &__arrow {
      display: none;
    }
    &__slide-price {
      .btn-after {
        font-size: 1.7rem;
      }
      span {
        font-size: 1.3rem;
      }
    }
  }

  .faq {
    &__btn {
      font-size: 1.5rem;
      text-align: center;
      line-height: 1.5;
    }
  }
  .card-body {
    font-size: 1.5rem;
    line-height: 1.8;
  }

  .big-slider {
    img {
      width: auto;
      height: 70vh;
    }
    &__slider-content {
      max-height: 320px;
      p {
        font-size: 1.5rem;
      }
    }
  }
  .footer {
    &__wrap {
      margin-top: 25px;
    }
    &__socials {
      flex-direction: column;
      align-items: center;
    }
    &__menu {
      flex-basis: 40%;
      font-size: 1.2rem;
    }
    &__logo {
      &:before {
        width: 80px;
      }
      &:after {
        width: 80px;
      }
    }
  }
  .subscribe {
    width: 90%;
    &__title {
      font-size: 1.5rem;
    }
    &__copyright {
      margin: 25px 0;
    }
  }
}
